body {
  font-family: $font-family;
  font-size: $font-size;
  line-height: $line-height;
  color: $text-color;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: $font-family;
  font-weight: 700;
  line-height: $line-height;
  color: $title-color;
}

h1 {
  margin-top: 50px;
  margin-bottom: 40px;
  font-weight: 800;
}

h1 {
  font-size: 22px;
  text-transform: uppercase;

  @media (min-width: $screen-md) {
    font-size: 40px;
  }

  @media (min-width: $screen-xl) {
    font-size: 40px;
  }
}

h2 {
  margin-bottom: 40px;
  font-size: 20px;

  @media (min-width: $screen-md) {
    font-size: 30px;
  }

  @media (min-width: $screen-xl) {
    font-size: 30px;
  }
}

h3 {
  margin-bottom: 15px;
  font-size: 16px;

  @media (min-width: $screen-md) {
    font-size: 24px;
  }

  @media (min-width: $screen-xl) {
    font-size: 24px;
  }
}

h4 {
  font-size: 14px;

  @media (min-width: $screen-md) {
    font-size: 18px;
  }

  @media (min-width: $screen-xl) {
    font-size: 18px;
  }
}

p,
ul,
ol,
dl,
blockquote,
pre,
address,
figure {
  margin-top: ($line-height / 2) + px;
  margin-bottom: ($line-height / 2) + px;
  color: currentColor;

  @media (min-width: $screen-md) {
    font-size: 16px;
  }
}

address {
  font-style: normal;
}

ul,
ol {
  padding-left: 1.2em;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-top: 0;
  margin-bottom: 0;
}

li {
  line-height: inherit;
}

hr {
  margin-top: $line-height + px;
  margin-bottom: $line-height + px;
  border: 0;
  border-top: 1px solid $border-color;
}

sub,
sup {
  font-size: 70%;
}

b,
strong {
  font-weight: bolder;
}

abbr[title] {
  text-decoration: underline dotted;
}

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0;
}
